"use client";

import React, { useState } from "react";
import axios from "axios";

const API_BASE_URL = "http://127.0.0.1:8000/api/qa/query"; // âœ… matches backend

interface AskQuestionBoxProps {
  selectedDocId: string | null;
}

export default function AskQuestionBox({ selectedDocId }: AskQuestionBoxProps) {
  const [question, setQuestion] = useState("");
  const [answer, setAnswer] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleAsk = async () => {
    if (!selectedDocId) {
      setError("Please select a document first.");
      return;
    }
    if (!question.trim()) {
      setError("Please enter a question.");
      return;
    }

    try {
      setLoading(true);
      setError(null);
      setAnswer(null);

      // âœ… Matches your cURL exactly
      const res = await axios.post(
        `${API_BASE_URL}?document_id=${selectedDocId}&question=${encodeURIComponent(question)}`,
        {},
        { headers: { accept: "application/json" } }
      );

      console.log("Response Data:", res.data);
      setAnswer(res.data.answer || JSON.stringify(res.data, null, 2));
    } catch (err: any) {
      console.error("Axios Error:", err);
      setError(`Error: ${err.response?.status} ${err.response?.statusText}`);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="p-4 bg-white shadow-md rounded-xl mt-6">
      <h2 className="text-lg font-semibold mb-2">Ask a Question</h2>

      <input
        type="text"
        placeholder="Type your question..."
        value={question}
        onChange={(e) => setQuestion(e.target.value)}
        className="border p-2 w-full rounded-md mb-3"
      />

      <button
        onClick={handleAsk}
        disabled={loading}
        className="bg-blue-600 hover:bg-blue-700 text-white py-2 px-4 rounded-md"
      >
        {loading ? "Asking..." : "Ask Question"}
      </button>

      {error && <p className="text-red-600 mt-3">{error}</p>}

      {answer && (
        <div className="bg-gray-100 border mt-4 p-3 rounded-md">
          <strong>Answer:</strong>
          <p className="whitespace-pre-wrap">{answer}</p>
        </div>
      )}
    </div>
  );
}