"use client";

import { useEffect, useState } from "react";
import axios from "axios";
import { toast } from "sonner";

interface Document {
  document_id: string;
  filename: string;
  status: string;
  chunks_created: number;
  uploaded_at: string;
}

export default function DocumentList({ onSelect }: { onSelect: (id: string) => void }) {
  const [documents, setDocuments] = useState<Document[]>([]);
  const [loading, setLoading] = useState(false);

  const fetchDocuments = async () => {
    setLoading(true);
    try {
      const res = await axios.get("http://127.0.0.1:8000/api/documents/");
      setDocuments(res.data);
    } catch (err) {
      toast.error("Failed to load documents.");
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  const deleteDocument = async (id: string) => {
    try {
      await axios.delete(`http://127.0.0.1:8000/api/documents/${id}`);
      toast.success("Document deleted successfully!");
      fetchDocuments();
    } catch (err) {
      toast.error("Failed to delete document.");
      console.error(err);
    }
  };

  useEffect(() => {
    fetchDocuments();
  }, []);

  return (
    <div className="bg-white p-6 rounded-2xl shadow-md">
      <h2 className="text-xl font-semibold mb-3 text-blue-600">Uploaded Documents</h2>

      {loading ? (
        <p>Loading documents...</p>
      ) : documents.length === 0 ? (
        <p className="text-gray-500">No documents found.</p>
      ) : (
        <ul className="space-y-2">
          {documents.map((doc) => (
            <li key={doc.document_id} className="flex justify-between items-center bg-gray-50 p-3 rounded-lg">
              <div>
                <p className="font-medium">{doc.filename}</p>
                <p className="text-sm text-gray-500">Status: {doc.status}</p>
              </div>
              <div className="flex gap-3">
                <button
                  onClick={() => onSelect(doc.document_id)}
                  className="bg-green-500 text-white px-3 py-1 rounded-lg hover:bg-green-600"
                >
                  Ask
                </button>
                <button
                  onClick={() => deleteDocument(doc.document_id)}
                  className="bg-red-500 text-white px-3 py-1 rounded-lg hover:bg-red-600"
                >
                  Delete
                </button>
              </div>
            </li>
          ))}
        </ul>
      )}
    </div>
  );
}